      center: [51.505, -0.159],
      markerLatLng: [51.504, -0.159]

    };
  },
  mounted() {
    this.$http.get(`objects/${this.$route.query.id}/`,{
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`,
      },
    })
    .then(res=>{
      this.objects_views=res.data
    })
  }
}
