          </div>
          <div>
            <a href="http://127.0.0.1:8000/api/v1/export-products-to-excel/" class="btn download_btn" @click="exportExe()">

              <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                <g id="download">
                  <path id="Icon" d="M17.5 12.5V15.8333C17.5 16.2754 17.3244 16.6993 17.0118 17.0118C16.6993 17.3244 16.2754 17.5 15.8333 17.5H4.16667C3.72464 17.5 3.30072 17.3244 2.98816 17.0118C2.67559 16.6993 2.5 16.2754 2.5 15.8333V12.5M5.83333 8.33333L10 12.5M10 12.5L14.1667 8.33333M10 12.5V2.5" stroke="#344054" stroke-width="1.67" stroke-linecap="round" stroke-linejoin="round"/>
                </g>
              </svg>
              Экспорт
            </a>

          </div>
        </div>
      </div>
      <div class="object_filt">
        <div class="filt_text_head">
          Район Алмазар
        </div>
        <div class="filt_main">

          <div class="fliter_header">
            <div class="filter_action">
              <select class="form-control" name="cars" id="cars" v-model="objectItem" @change="getregion()">
                <option v-for="(item,index) in options1" :key="index" :value="item">{{item.name}}</option>
              </select>
            </div>
            <div class="mx-3 filter_action">
              <select class="form-control" name="cars" id="cars" v-model="objectItem1" @change="getkvartals()">
                <option v-for="(item,index) in options2" :key="index" :value="item">{{item.name}}</option>
              </select>
            </div>
            <div class="mx-3  filter_action">
              <select class="form-control" name="cars" id="cars" v-model="objectItem2" @change="getDistrict()">
                <option v-for="(item,index) in options3" :key="index" :value="item">{{item.name}}</option>
              </select>
            </div>
            <div class="mx-3  filter_action">
              <input type="text" class="form-control" v-model="search"  placeholder="" aria-label="Username"
                     aria-describedby="basic-addon1">
            </div>
          </div>
          <div>
            <button class="filt_btn" @click="clearFunc()">
              <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                <g clip-path="url(#clip0_59_21008)">
                  <path d="M19.1665 3.33333V8.33333H14.1665" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                  <path d="M0.833496 16.6667V11.6667H5.8335" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                  <path d="M2.92516 7.5C3.3478 6.30565 4.06611 5.23783 5.01305 4.39618C5.96 3.55452 7.10472 2.96647 8.3404 2.68688C9.57609 2.40729 10.8625 2.44528 12.0795 2.79731C13.2965 3.14934 14.4045 3.80392 15.3002 4.7L19.1668 8.33333M0.833496 11.6667L4.70016 15.3C5.59579 16.1961 6.70381 16.8507 7.92084 17.2027C9.13787 17.5547 10.4242 17.5927 11.6599 17.3131C12.8956 17.0335 14.0403 16.4455 14.9873 15.6038C15.9342 14.7622 16.6525 13.6943 17.0752 12.5" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                </g>
                <defs>
                  <clipPath id="clip0_59_21008">
                    <rect width="20" height="20" fill="white"/>
                  </clipPath>
                </defs>
              </svg>
              Филтрь
            </button>
          </div>
        </div>
      </div>
      <div class="create_f">

        <button class="print_btn" @click="$router.push('/objects/create')">
          <b-icon icon="plus-square" aria-hidden="true"></b-icon>
          Create
        </button>
      </div>
      <div class="passport">
        <table class="table ">
          <thead class="thead-dark">
          <tr>
            <th scope="col">T/R</th>
            <th scope="col">Obyekt turi</th>
            <th scope="col">Obyekt maqsad vazifasi</th>
            <th scope="col">Obyekt nomi</th>
            <th scope="col">Obyekt tasnifi (Jaxon,Respublika,mahalliy)</th>
            <th scope="col">Obyekt tasnifi (Turar, no turar)</th>
            <th scope="col">Obyekt davri</th>
            <th scope="col">Viloyat</th>
            <th scope="col">Tuman</th>
            <th scope="col">Manzil</th>
            <th scope="col">Kadastr raqami</th>
            <th scope="col">Ko'chmas mulk huquqi</th>
            <th scope="col">Action</th>
          </tr>
          </thead>
          <tbody>

          <tr  v-for="(item,index) in objects_views" :key="index">
            <td scope="row">{{item.id}}</td>
            <td>{{item.object_type_id}}</td>
            <td>{{item.object_goal_id}}</td>
            <td scope="row">{{item.name}}</td>
            <td>{{item.object_desk_id}}</td>
            <td>{{item.object_desk1_id}}</td>
            <td scope="row">{{item.object_age}}</td>
            <td>{{item.regions_name}}</td>
            <td>{{item.district_name}}</td>
            <td scope="row">{{item.address}}</td>
            <td>{{item.k_number}}</td>
            <td>{{item.author}}</td>

            <td>
              <div class="btn_full">

                <button class="btn btn-primary "> <b-icon icon="pencil-fill" aria-hidden="true"></b-icon></button>
                <button class="btn btn-outline-primary mx-2" @click="$router.push(`/objects/view?id=${item.id}`)"> <b-icon icon="eye-fill" aria-hidden="true"></b-icon></button>
                <button class="btn btn-danger" @click="deletes(item.id)"> <b-icon icon="trash-fill" aria-hidden="true"></b-icon></button>
              </div>
            </td>
          </tr>
          </tbody>
        </table>

      </div>
      <div>
        <b-pagination
            v-model="currentPage"
            :total-rows="100"
            :per-page="perPage"
            aria-controls="my-table"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import Navbar from "@/components/Navbar.vue";
import Destricts from '@/Json/districts.json'
import Regions from '@/Json/regions.json'
import Kvartals from '@/Json/kvartals.json'
export default {
  components:{
    Navbar,
    Sidebar,
  },
  data() {
    return {
      perPage: 3,
      currentPage: 1,
      items: [
        { id: 1, first_name: 'Fred', last_name: 'Flintstone' },
        { id: 2, first_name: 'Wilma', last_name: 'Flintstone' },
        { id: 3, first_name: 'Barney', last_name: 'Rubble' },
        { id: 4, first_name: 'Betty', last_name: 'Rubble' },
        { id: 5, first_name: 'Pebbles', last_name: 'Flintstone' },
        { id: 6, first_name: 'Bamm Bamm', last_name: 'Rubble' },
        { id: 7, first_name: 'The Great', last_name: 'Gazzoo' },
        { id: 8, first_name: 'Rockhead', last_name: 'Slate' },
        { id: 9, first_name: 'Pearl', last_name: 'Slaghoople' }
      ],

      search:'',
      stats:[],
      sportsman:[],

      selected: [], // Must be an array reference!
      options: [
      ],
      gender:'Erkak',
      name:'',
      district_name:'',
      content:'',
      date:new Date(),
      options1: [
      ],
      options2: [
      ],
      options3: [
      ],
      options4: [
        {
          code:1,
          name:"Hokim sektori"
        },
        {
          code:2,
          name:"Prokuror sektori"
        },
        {
          code:3,
          name:"Ichki Ishlar sektori"
        },
        {
          code:4,
          name:"Davlat Soliq sektori"
        }
      ],
      options5: [
        {
          code:1,
          name:"Workout"
        },
        {
          code:2,
          name:"Stritbol"
        },
        {
          code:3,
          name:"Voleybol"
        },
        {
          code:4,
          name:"Shashka"
        },
        {
          code:5,
          name:"Stol tennis"
        },
        {
          code:6,
          name:"Yengil atletika"
        },
      ],

      options6: [
        {
          code:1,
          name:"7-10"
        },
        {
          code:2,
          name:"11-15"
        },
        {
          code:3,
          name:"16-21"
        },
        {
          code:4,
          name:"22-26"
        },
        {
          code:5,
          name:"27-30"
        },
      ],
      options7:[],

      options8: [
        {
          code:1,
          name:"O'g'il"
        },
        {
          code:2,
          name:"Qiz"
        },
      ],
      objectItem: {},
      objectItem1: {},
      objectItem2: {},
      objectItem3: {},
      objectItem4: {},
      objectItem5: {},
      objectItem6: {},
      objectItem7: {},
      stringItem: '',
      regions: [],
      minDate: '1950',
      maxDate: '2040',
      isFilt:'',
      filTname:'',
      objects_views:[],
      user_id:''
    }
  },
  mounted() {
    this.city=Regions
    this.region=Destricts
    this.destricts=Kvartals


    Regions.forEach(el=>{
      this.options1.push({
        code:el.id,
        name:el.name
      })
    })

    this.$http.get(`user-me/`,{
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`,
      },
    })
        .then(res=>{
          this.user_id=res.data.id
          this.$http.get(`objects/?user_id=${res.data.id}`,{
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          })
              .then(res=>{
                this.objects_views=res.data
              })
        })


  },
  methods: {



    getregion(){
      console.log(this.objectItem)
      this.options2=[]
      this.options3=[]

      Destricts.filter(tree => tree.parent === this.objectItem.code).forEach(el=>{
        this.options2.push({
          code:el.id,
          name:el.name
        })
      })
      this.$http.get(`objects/?regions=${this.objectItem.code}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })

    },
    exportExe(){
      this.$http.get(`export-products-to-excel/`)
    },
    getkvartals(){
      Kvartals.filter(tree => tree.parent === this.objectItem1.code).forEach(el=>{
        this.options3.push({
          code:el.id,
          name:el.name
        })
      })

      this.$http.get(`objects/?regions=${this.objectItem.code}&&district=${this.objectItem1.code}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })
    },
    getDistrict(){

      this.$http.get(`objects/?regions=${this.objectItem.code}&&address_id=${this.objectItem1.code}&&address_id=${this.objectItem2.code}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })

    },

    searchFilt(){

      if(this.search!=undefined){
        this.filTname=this.filTname+`&name=${this.search}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
          })
    },

    clearFunc(){
      this.$http.get('objects/',{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
            this.objectItem1=[]
            this.objectItem=[]
            this.objectItem2=[]
          })
    },
    deletes(item){
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        this.$http.delete(`/objects/${item}/`,
            {
              headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
                'Accept-Language': 'uz-latin'
              }
            })
            .then(()=>{

              this.$toast("Малумот ўчирилди", {
                timeout: 2000,
                type:"success"
              });
              this.$http.get(`/objects/`,
                  {
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                  })
                  .then(res=>{
                    this.objects_views=res.data
                  })
            })

      }
    }
  }
}
</script>